* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: black;
}
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 1em;
  text-align: center;
  background-color: black;
}
.nav-right ul {
  display: flex;
  list-style: none;
  align-items: center;
}
.nav-right ul li {
  margin-right: 1rem;
}
main {
  width: 100vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.container {
  width: 60vw;
  padding: 0.5rem;
  word-wrap: wrap;
  color: white;
}
textarea {
  margin-top: 1rem;
  box-shadow: 2 2 0.5 black;
  outline: none;
  color: white;
  padding: 0.1em;
  border: rgb(153, 148, 159) solid 2px;
  border-radius: 0.2rem;
  background-color: rgb(58, 61, 64);
  transition: 0.2s all ease-in-out;
  width: 100%;
}
textarea:focus {
  border: rgb(85, 37, 174) solid 2px;
}
textarea:hover {
  border: rgb(85, 37, 174) solid 2px;
}
.btns {
  word-wrap: wrap;
  align-items: center;
  margin-top: 1rem;
}
button {
  background-color: rgb(85, 37, 174);
  cursor: pointer;
  padding: 0.5rem;
  color: white;
  border: none;
  border-radius: 0.5rem;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  transition: 0.1s all ease-in-out;
}
button:hover {
  background-color: rgb(59, 24, 124);
}
.summ {
  color: white;
  padding: 1em;
}
.summ p {
  word-wrap: break-word;
}

.summ h2 {
  margin-top: 1rem;
}
.clearBtn {
  background-color: rgb(157, 44, 21);
}
.clearBtn:hover {
  background-color: rgb(103, 39, 28);
}
.copyBtn {
  background-color: rgb(23, 23, 23);
}
.copyBtn:hover {
  background-color: rgb(84, 84, 84);
}
a {
  text-decoration: none;
  color: white;
}
a:hover {
  text-shadow: gray 5px 5px 5px;
  color: gray;
}
